ontent .search-input input{flex:1;border:0;outline:0;font-size:14px;line-height:1.6;height:32px;color:#444;padding:0 10px;background:#f3f3f3}.search-content .search-input button{display:inline-block;width:60px;height:32px;margin-left:6px;background:#00b51d;outline:0;border:0;text-align:center;color:#fff;font-size:14px;border-radius:4px}.loading-box{height:40px;line-height:40px;text-align:center}.loading-box .icon-jiazai{display:inline-block;vertical-align:middle;font-size:24px;color:#ccc;animation:1s rotate linear infinite}@keyframes rotate{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.site-footer{z-index:100;position:fixed;bottom:0;left:0;right:0;display:flex;box-shadow:0 3px 14px 2px rgba(0,0,0,0.12);background:#fff}.site-footer .item{flex:1;padding:4px 0;color:#666;text-align:center}.site-footer .item.active{color:#00b51d}.site-footer .iconfont{display:block;font-size:20px;line-height:24px}.site-footer .word{font-size:12px;line-height:16px}
